h1 {
  margin: 0;
  font-weight: 800; }
  @media (min-width: 0em) {
    h1 {
      font-size: 24px; } }
  @media (min-width: 37.5em) {
    h1 {
      font-size: 36px; } }

a:link {
  color: white;
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

a:visited {
  color: inherit; }

.project-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 260px;
  grid-row-gap: 24px;
  grid-column-gap: 48px; }
  @media (min-width: 0em) {
    .project-container {
      padding-left: 8px;
      padding-right: 8px; } }
  @media (min-width: 50em) {
    .project-container {
      grid-template-columns: repeat(2, 1fr);
      padding-left: 24px;
      padding-right: 24px; } }
  @media (min-width: 70em) {
    .project-container {
      grid-template-columns: repeat(3, 1fr); } }



