h1 {
  margin: 0;
  font-weight: 800; }
  @media (min-width: 0em) {
    h1 {
      font-size: 24px; } }
  @media (min-width: 37.5em) {
    h1 {
      font-size: 36px; } }

a:link {
  color: white;
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

a:visited {
  color: inherit; }

.home {
