h1 {
  margin: 0;
  font-weight: 800; }
  @media (min-width: 0em) {
    h1 {
      font-size: 24px; } }
  @media (min-width: 37.5em) {
    h1 {
      font-size: 36px; } }

a:link {
  color: white;
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

a:visited {
  color: inherit; }

@media (min-width: 0em) {
  header {
    margin-bottom: 16px; } }

@media (min-width: 37.5em) {
  header {
    margin-bottom: 32px; } }

header .header-content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between; }

header .social-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between; }
  @media (min-width: 0em) {
    header .social-links .social-link {
      width: 24px;
      height: 24px;
      margin-left: 16px; } }
  @media (min-width: 37.5em) {
    header .social-links .social-link {
      width: 28px;
      height: 28px;
      margin-left: 24px; } }
  header .social-links .social-link .svg-path {
    fill: #FFFFFF; }
  header .social-links .social-link:hover .svg-path.twitter-icon {
    fill: #1DA1F2; }
  header .social-links .social-link:hover .svg-path.linkedin-icon {
    fill: #0077B5; }

header hr {
  border: 1px solid #232323;
  margin-top: 4px;
  margin-bottom: 0; }



