h1 {
  margin: 0;
  font-weight: 800; }
  @media (min-width: 0em) {
    h1 {
      font-size: 24px; } }
  @media (min-width: 37.5em) {
    h1 {
      font-size: 36px; } }

a:link {
  color: white;
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

a:visited {
  color: inherit; }

.about-container {
  display: -ms-flexbox;
  display: flex; }
  .about-container .profile-pic {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    border-radius: 50%; }
    @media (min-width: 0em) {
      .about-container .profile-pic {
        width: 80px;
        height: 80px;
        margin-right: 24px; } }
    @media (min-width: 37.5em) {
      .about-container .profile-pic {
        width: 140px;
        height: 140px;
        margin-left: 24px;
        margin-right: 24px; } }
    @media (min-width: 50em) {
      .about-container .profile-pic {
        width: 200px;
        height: 200px;
        margin-left: 40px;
        margin-right: 40px; } }
  .about-container .about-content p {
    font-weight: 500; }
    @media (min-width: 37.5em) {
      .about-container .about-content p {
        font-size: 18px; } }
    @media (min-width: 50em) {
      .about-container .about-content p {
        font-size: 20px; } }



