h1 {
  margin: 0;
  font-weight: 800; }
  @media (min-width: 0em) {
    h1 {
      font-size: 24px; } }
  @media (min-width: 37.5em) {
    h1 {
      font-size: 36px; } }

a:link {
  color: white;
  text-decoration: none; }

a:hover {
  text-decoration: underline; }

a:visited {
  color: inherit; }

.pablo {
  width: 100%;
  height: 100vh;
  margin: 0;
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: bold;
  color: #000000;
  text-transform: uppercase; }
  .pablo .background {
    margin: 0;
    width: 100%;
    height: 100%;
    background-color: #F1F1F1; }
  .pablo .album-cover {
    margin: 0 auto;
    background-color: #F28C5E;
    position: relative; }
  .pablo .content-table {
    position: relative;
    top: 3%; }
  .pablo .large-text {
    letter-spacing: -0.4px; }
    .pablo .large-text .col-space {
      width: 20px; }
  .pablo a:link {
    color: inherit;
    text-decoration: none; }
  .pablo a:hover {
    text-decoration: underline; }
  .pablo a:visited {
    color: inherit; }
  .pablo img {
    z-index: 1;
    position: absolute; }
  @media (min-width: 0em) {
    .pablo .album-cover {
      width: 100%; }
    .pablo .content-table {
      width: 97%;
      left: 3%; }
    .pablo .small-text {
      font-size: 2vh;
      line-height: 2.2vh; }
    .pablo .large-text {
      font-size: 3.8vh;
      line-height: 4vh; }
    .pablo .pic1 {
      left: 20%;
      top: 26%;
      height: 25%; }
    .pablo .pic2 {
      left: 45%;
      top: 68%;
      height: 20%; } }
  @media (min-width: 64em) {
    .pablo .album-cover {
      height: 100%; }
    .pablo .content-table {
      width: 100%;
      left: 12%; }
    .pablo .small-text {
      font-size: 2.4vh;
      line-height: 2.6vh;
      letter-spacing: -0.4px; }
    .pablo .large-text {
      font-size: 6.2vh;
      line-height: 7.2vh; }
    .pablo .pic1 {
      left: 20%;
      top: 30%;
      height: 25%; }
    .pablo .pic2 {
      left: 45%;
      top: 67%;
      height: 20%; } }



